.left{
    background-color: #5d30ff;
    height: 370px;
    width: 290px;
    display: flex;
   flex-direction: column;
  align-content: center;
  text-align: center;
  border-radius: 25px;

}
.leftWrapper{
    width: 80%;
margin: auto;
/* margin-right: auto; */
/* margin-bottom: auto; */
/* margin-left: auto; */
}
.leftHeading{
    padding: 20px;
}
.leftHeading span{
    color: rgb(180, 180, 180);
    font-weight: 500;
}
.digit{
    display: flex;
justify-content: center;
}
.digitWrapper{
    height: 150px;
    width: 150px;
    background:  linear-gradient(rgb(0, 17, 201),#5d30ff) ;
    border-radius: 50%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  
}
.digitWrapper p{
    color:rgb(180, 180, 180);
}
 .digitHead{
    font-size: 48px;
    font-weight: 500;
    color: white;
}
.leftInfo{
    margin-top: 16px;
}
.leftInfoHead{
    font-weight: 500;
    font-size: 24px;
    color: white;
    
}
.leftInfo p{
    color: rgb(180, 180, 180);
    margin-top: 20px;
}
.right{
    /* background-color: yellow; */
    height: 370px;
width: 290px;
display: flex;
flex-direction: column;
align-content: center;
}
.rightWrapper{
    width: 80%;
    margin: auto;
}
.rightHeader{
    padding-top: 20px;
    padding-bottom: 20px;
}
.rightHeader span{
    font-weight: 500;
}
.rightCol >div{
    padding: 12px;
    /* background-color: green; */
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
    border-radius: 8px;
}
.col1{
    background-color: rgba(248, 180, 180, 0.164);
    color: rgb(192, 61, 1);
}
.col1 div{
    display: flex;
}
.col1 div svg{
    width: 22px;
/* gap: 20px; */
margin-right: 10px;
}
.col2{
    background-color: rgba(248, 204, 60, 0.192);
    color:rgb(255, 217, 0);
}
.col2 div{
    display: flex;
}
.col2 div svg{
    width: 22px;
/* gap: 20px; */
margin-right: 10px;
}
.col3{
    background-color: rgba(122, 255, 193, 0.336);
    color: rgb(0, 179, 0);
}
.col3 div{
    display: flex;
}
.col3 div svg{
    width: 22px;
/* gap: 20px; */
margin-right: 10px;
}
.col4{
    background-color: rgba(180, 142, 250, 0.39);
    color: rgb(46, 0, 153);
}
.col4 div{
    display: flex;
}
.col4 div svg{
    width: 22px;
/* gap: 20px; */
margin-right: 10px;
}
.button{
    padding: 12px;
    background-color: rgb(0, 32, 61)  ;
    border-radius: 20px;
    text-align: center;
    color: white;
    transition: all 1s ease;
    cursor: pointer;
}
.button:hover{
    background-color: #5d30ff;
}
.svg{
    /* width: 30px; */
    width: 22px;
/* gap: 20px; */
margin-right: 10px;
color: rgb(255, 217, 0);
}
@media screen and (max-width:500px) {
    .mainCont{
        display: block;
    }
    .mainContWrapper{
        display:block;
        border-radius: 0;
        box-shadow: none;
    }
    .left{
        width: 100%;
        width: 100%;
border-top-left-radius: 0;
border-top-right-radius: 0;


    }
    .right{
        width: 100%;
    }
}